<h2 id="dialog-弹窗" tabindex="-1"><a class="header-anchor" href="#dialog-弹窗" aria-hidden="true">#</a> Dialog 弹窗</h2>
<h3 id="用法" tabindex="-1"><a class="header-anchor" href="#用法" aria-hidden="true">#</a> 用法</h3>
<h4 id="基本用法" tabindex="-1"><a class="header-anchor" href="#基本用法" aria-hidden="true">#</a> 基本用法</h4>
<div class="component-box">
  <div class="component-box-top"><ClientOnly>
    <wd-button @click="centerDialogVisible = true">show dialog</wd-button>
    <wd-dialog
      v-model="centerDialogVisible"
      title="Notice"
      width="40%"
      :show-close="true"
      @open="openHandler"
      @before-open="beforeOpen"
      @before-close="beforeClose"
      :append-to-body="false"
      :closeOnClickModal="false"
      :destroy-on-close="true"
    >
      <span
        >Notice: before dialog gets opened for the first time this node and the
        one bellow will not be rendered</span
      >
    </wd-dialog>
    </ClientOnly>
  </div>
  <div class="component-box-bottom">
    <div class="component-title">对话框组件</div>
    <div class="component-desc">
      弹出一个对话框，提示用户确认信息
    </div>
  </div>
</div>
